// Here you can create and customize the pages' footer:

@use "../globals" as *;
@use "../helpers" as *;
@use "../mixins" as *;

%footer {
  text-align: center;
  background-color: black;
  padding-top: $txt-padding-md;
  .container {
    @include breakpoint(tablet) {
      .col {
        display: flex;
        column-gap: 40px;
        margin-bottom: 30px;
        justify-content: space-around;
      }
      .links {
        text-align: start;
      }
      .social {
        text-align: start;
      }
    }
    @include breakpoint(desktop) {
      display: flex;
      column-gap: 40px;
    }
  }
  .col:first-of-type {
    .box {
      h3 {
        color: $white;
      }
      p {
        color: $txt-clr;
        max-width: 400px;
        margin: 15px auto;
      }
      > a {
        width: 150px;
        display: block;
        margin: 10px auto;
        color: $txt-clr;
      }
    }
  }
  .links {
    @media (max-width: 767px) {
      margin-bottom: 30px;
    }
    h3 {
      font-size: 18px;
      font-weight: bold;
      color: $white;
      margin-bottom: 20px;
    }
    a {
      display: block;
      margin-top: 5px;
      color: $txt-clr;
      transition: $transition;
      &:hover {
        color: $white;
      }
    }
  }
  .social {
    h3 {
      font-size: 18px;
      font-weight: bold;
      color: $white;
      margin-bottom: 15px;
    }
    span {
      display: block;
      a {
        transition: $transition;
        color: $txt-clr;
        &:hover {
          color: $white;
        }
      }
    }
    i {
      padding: 10px;
      margin-right: 10px;
      border-radius: $border-radius;
      &:hover {
        background-color: #1b1a1a;
      }
    }
    .fb {
      color: #003cff;
    }
    .tw {
      color: #0f6aed;
    }
    .ins {
      color: #f15315;
    }
    .yt {
      color: #db0b0b;
    }
  }
  .copyright {
    border-top: 1px solid white;
    margin: 20px auto 0;
    padding: 10px 0;
    p {
      text-transform: capitalize;
      color: $white;
      span:first-of-type {
        color: $main-clr;
      }
      span:last-of-type {
        color: $main-clr;
      }
    }
  }
}
